export default {
	data() {
		return {
			shenqlxIndex: -1,
			shenqlx: ['一般申请', '延续申请', '变更申请'],
			zrr: true,
			fr: false,
			shenqzlbIndex: 0,
			shenqzlb: ['自然人', '法人'],
			zhengjlxIndex: 0,
			zhengjlx: ['身份证', '护照', '居住证', '台胞证', '通行证'],
			frzhengjlxIndex: 0,
			frzhengjlx: ['身份证', '护照', '居住证', '台胞证', '通行证'],
			index: -1,
			picker: ['喵喵喵', '汪汪汪', '哼唧哼唧'],
			multiArray: [
				['江苏省'],
				['南京市', '无锡市', '徐州市', '常州市', '苏州市', '南通市',
				 '淮安市', '连云港市', '盐城市', '扬州市', '镇江市', '泰州市', '宿迁市'],
				['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区']
			],
			objectMultiArray: [
				[{
						id: 0,
						name: '无脊柱动物'
					},
					{
						id: 1,
						name: '脊柱动物'
					}
				],
				[{
						id: 0,
						name: '扁性动物'
					},
					{
						id: 1,
						name: '线形动物'
					},
					{
						id: 2,
						name: '环节动物'
					},
					{
						id: 3,
						name: '软体动物'
					},
					{
						id: 3,
						name: '节肢动物'
					}
				],
				[{
						id: 0,
						name: '猪肉绦虫'
					},
					{
						id: 1,
						name: '吸血虫'
					}
				]
			],
			multiIndex: [0, 0, 0],
			time: '12:01',
			date: '2018-12-25',
			region: ['江苏省', '南京市', '鼓楼区'],
			switchA: false,
			switchB: true,
			switchC: false,
			switchD: false,
			radio: 'A',
			checkbox: [{
				value: 'A',
				checked: true
			}, {
				value: 'B',
				checked: true
			}, {
				value: 'C',
				checked: false
			}],
			imgList: [],
			modalName: null,
			textareaAValue: '',
			textareaBValue: ''
		};
	},
	methods: {
		shenqlxChange(e) {
			this.shenqlxIndex = e.detail.value
		},
		shenqzlbChange(e) {
			this.shenqzlbIndex = e.detail.value;
			if(this.shenqzlbIndex === 0){
				this.fr = false;
				this.zrr = true;
			} else {
				this.fr = true;
				this.zrr = false;
			}
		},
		zhengjlxChange(e) {
			this.zhengjlxIndex = e.detail.value
		},
		frzhengjlxChange(e) {
			this.frzhengjlxIndex = e.detail.value
		},
		PickerChange(e) {
			this.index = e.detail.value
		},
		MultiChange(e) {
			this.multiIndex = e.detail.value
		},
		MultiColumnChange(e) {
			let data = {
				multiArray: this.multiArray,
				multiIndex: this.multiIndex
			};
			data.multiIndex[e.detail.column] = e.detail.value;
			switch (e.detail.column) {
				case 0:
					switch (data.multiIndex[0]) {
						case 0:
							data.multiArray[1] = ['扁性动物', '线形动物', '环节动物', '软体动物', '节肢动物'];
							data.multiArray[2] = ['猪肉绦虫', '吸血虫'];
							break;
						case 1:
							data.multiArray[1] = ['鱼', '两栖动物', '爬行动物'];
							data.multiArray[2] = ['鲫鱼', '带鱼'];
							break;
					}
					data.multiIndex[1] = 0;
					data.multiIndex[2] = 0;
					break;
				case 1:
					switch (data.multiIndex[0]) {
						case 0:
							switch (data.multiIndex[1]) {
								case 0:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
								case 1:
									data.multiArray[2] = ['崇安区', '南长区', '北塘区', '锡山区', '惠山区', '滨湖区', '江阴市', '宜兴市'];
									break;
								case 2:
									data.multiArray[2] = ['鼓楼区', '云龙区', '贾汪区', '泉山区', '铜山区', '丰县', '沛县', '睢宁县', '新沂市', '邳州市'];
									break;
								case 3:
									data.multiArray[2] = ['虎丘区', '吴中区', '相城区', '姑苏区', '吴江区', '常熟市', '张家港市', '昆山市', '太仓市'];
									break;
								case 4:
									data.multiArray[2] = ['崇川区', '港闸区', '通州区', '海安县', '如东县', '启东市', '如皋市', '海门市'];
									break;
								case 5:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
								case 6:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
								case 7:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
								case 8:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
								case 9:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
								case 10:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
								case 11:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
								case 12:
									data.multiArray[2] = ['鼓楼区', '建邺区', '玄武区', '秦淮区', '浦口区', '栖霞区', '雨花台区', '六合区', '溧水区', '高淳区'];
									break;
							}
							break;
						case 1:
							switch (data.multiIndex[1]) {
								case 0:
									data.multiArray[2] = ['鲫鱼', '带鱼'];
									break;
								case 1:
									data.multiArray[2] = ['青蛙', '娃娃鱼'];
									break;
								case 2:
									data.multiArray[2] = ['蜥蜴', '龟', '壁虎'];
									break;
							}
							break;
					}
					data.multiIndex[2] = 0;
					break;
			}
			this.multiArray = data.multiArray;
			this.multiIndex = data.multiIndex;
		},
		TimeChange(e) {
			this.time = e.detail.value
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		RegionChange(e) {
			this.region = e.detail.value
		},
		SwitchA(e) {
			this.switchA = e.detail.value
		},
		SwitchB(e) {
			this.switchB = e.detail.value
		},
		SwitchC(e) {
			this.switchC = e.detail.value
		},
		SwitchD(e) {
			this.switchD = e.detail.value
		},
		RadioChange(e) {
			this.radio = e.detail.value
		},
		CheckboxChange(e) {
			var items = this.checkbox,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				items[i].checked = false;
				for (var j = 0, lenJ = values.length; j < lenJ; ++j) {
					if (items[i].value == values[j]) {
						items[i].checked = true;
						break
					}
				}
			}
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '一体化政务平台',
				content: '确定要删除吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		textareaBInput(e) {
			this.textareaBValue = e.detail.value
		}
	}
}
